/**
 * Class FreshRejuvenations
 */
class FreshRejuvenations {

  constructor() {
    //Store the position of the window scroll when the request consultation button becomes sticky
    this.windowScrollPosition = null;
    this.emailAddress = "pthorpe@ihug.com.au";
  }

  /**
   * Initialise the object
   */
  init() {
    this.initStickyRequestConsultation();
    this.initRequestConsultationModalSlideout();
    this.initSlider();
    this.initParallaxSeparator();
    this.initImages();
  }

  /**
   * Initialise the request consultation position so that when the window has scrolled down far enough,
   * it becomes fixed so it will always be visible on the page
   *
   * @returns null
   */
  initStickyRequestConsultation() {
    var $window = $(window);
    $window.on('scroll', this.debounce(() => {this.handlerWindowScroll()}, 15));
    $window.trigger('scroll');
  }

  /**
   * Initialise the request consultation modal slideout
   *
   * @returns null
   */
  initRequestConsultationModalSlideout() {
    $('.request-consultation-button').on('click', (e) => {this.handlerClickRequestConsultationButton(e)});
  }

  /**
   * Initialise the hero image slider
   */
  initSlider() {
    new Swiper(".swiper-container", {
      autoplay: 5000,
      pagination: '.swiper-pagination'
    });
  }

  /**
   * Initialise the separators with a parralax background effet
   */
  initParallaxSeparator() {
    new Parallaxify({
      elements: ".our-work__section-divider", // default
      backgroundYMin: 100, // default
      backgroundYMax: 0 // default
    }).registerUpdate();
  }

  /**
   * Loop through all images, taking them out of their <template> container, and putting them into the page DOM normally
   *
   * @returns null
   */
  initImages() {
    var $images = $(".images");

    $images.each((index, imageGroup) => {
      const $imageGroup     = $(imageGroup);
      const $imageTemplates = $imageGroup.find("template");
      $.each($imageTemplates, (i, image) => {
        let node = document.importNode(image.content, true);
        imageGroup.appendChild(node);
        image.remove();
      });
    });

    $(document).on('lity:open', () => {
      this.togglePageScroll(false);
    });
    $(document).on('lity:close', () => {
      this.togglePageScroll(true);
    });
  }

  /**
   * Update the position of the request consultation button when the window scrolls past the breakpoint
   *
   * @returns null
   */
  handlerWindowScroll() {
    const windowScroll = $(window).scrollTop();
    const $requestConsultationButtonContainer = $(".request-consultation-button-container");
    const deviceStickyPoint = this.getDeviceStickyBreakpoint();

    //Once the Request Consultation button container is 200px from the top, set it to sticky
    if ($requestConsultationButtonContainer.css('position') !== 'fixed' && this.getElementOffsetFromTopOfBrowser($requestConsultationButtonContainer) < deviceStickyPoint) {
      $requestConsultationButtonContainer.css('position', 'fixed');
      $requestConsultationButtonContainer.css('top', deviceStickyPoint+'px');
      this.windowScrollPosition = windowScroll; //Store the window scroll position when button becomes sticky
    } else if (this.windowScrollPosition > windowScroll) {
      //If the current window scroll is greater than the position at which the request consultation button became sticky, then it means it should no longer be
      $requestConsultationButtonContainer.removeAttr('style');
    }
  }

  /**
   * Handle the even when the request consultation button is clicked
   *
   * @param {Event} e The click event
   *
   * @returns null
   */
  handlerClickRequestConsultationButton(e) {
    e.preventDefault();
    this.displayRequestConsultationModal();
  }

  /**
   * Display the request consultation modal
   *
   * This function will also attached the events that will fire to close the modal
   *
   * @returns null
   */
  displayRequestConsultationModal() {
    let $requestConsultationBackgrounOverlay = $(".request-consultation_background-overlay");
    $requestConsultationBackgrounOverlay.toggleClass('off-canvas');

    $("#request-consultation-form").one('submit', (e) => {this.handlerSubmitRequestConsultationForm(e)});

    //Attach the click handlers
    $requestConsultationBackgrounOverlay.on('click', () => {this.closeRequestConsultationModal()});
    $('.request-consultation-modal__close-button').on('click', () => {this.closeRequestConsultationModal()});
    this.togglePageScroll(false);
  }

  /**
   * Close the request consultation modal
   *
   * This is done by adding the required class to the background overlay, triggering the CSS to hide both the overlay, and the modal iteself
   *
   * @return null
   */
  closeRequestConsultationModal() {
    let $requestConsultationBackgrounOverlay = $(".request-consultation_background-overlay");
    $requestConsultationBackgrounOverlay.addClass('off-canvas');

    //Remove the click handlers
    $requestConsultationBackgrounOverlay.off('click');
    $('.request-consultation-modal__close-button').off('click');
    this.togglePageScroll(true);

    $("#request-consultation-form").off('submit');
  }

  /**
   * Handle the even fired when the Request Consultation form is submitted.
   *
   * The contents of the form will be processed into a mailto URL, and then opened in a new window, so the user can send their email
   *
   * @param e
   *
   * @returns null
   */
  handlerSubmitRequestConsultationForm(e) {
    e.preventDefault();
    const $form = $(e.target);

    const name    = $form.find('#name').val();
    const email   = $form.find('#email').val();
    const details = $form.find('#details').val();

    window.open("mailto:"+this.emailAddress+"?subject="+encodeURI("Website Enquiry")+"&body="+encodeURI(details+"\n\nFrom "+name+"\nReply Email: "+email));
  }

  /**
   * Enabled/disable page scrolling
   *
   * @param {boolean} enabled Flag indicating if scroll should be enabled or disabled
   *
   * @returns null
   */
  togglePageScroll(enabled) {
    $("body").toggleClass('noScroll', !enabled);
  }

  /**
   * Get the px distance from the top of the element to the top edge of the browser window
   *
   * @param {jQuery} $element The element to get the offset for
   *
   * @returns {number}
   */
  getElementOffsetFromTopOfBrowser($element) {
    return $element.offset().top - $(window).scrollTop();
  }

  /**
   * Get the point at which the request consultation button should become sticky
   *
   * This is going to be different between small and large devices
   */
  getDeviceStickyBreakpoint() {
    const $window      = $(window);
    const windowWidth  = $window.outerWidth();
    const windowHeight = $window.outerHeight();

    if (windowWidth > 1000) {
      return 200;
    } else if (windowWidth > 500) {
      return 50;
    }

    return 10;
  }

  /**
   * Returns a function, that, as long as it continues to be invoked, will not
   * be triggered. The function will be called after it stops being called for
   * N milliseconds. If `immediate` is passed, trigger the function on the
   * leading edge, instead of the trailing.
   *
   * @param func
   * @param wait
   * @param immediate
   *
   * @returns {Function}
   */
  debounce(func, wait, immediate) {
    var timeout;
    return function() {
      var context = this, args = arguments;
      var later = function() {
        timeout = null;
        if (!immediate) func.apply(context, args);
      };
      var callNow = immediate && !timeout;
      clearTimeout(timeout);
      timeout = setTimeout(later, wait);
      if (callNow) func.apply(context, args);
    };
  };
}

$(document).ready(() => {
  const app = new FreshRejuvenations();
  app.init();
});
